import React, { Component } from 'react';
import { Redirect } from 'react-router-dom';

//import Header from '../components/Header';
import GoTo from '../components/GoTo';
import Footer from '../components/Footer';

import {
    config,
    INBOX_GOOGLE, 
    INBOX_OUTLOOK, 
    INBOX_IMAP,
    PROXY_CORS
 } from "../constants";

export class PageGoTo extends Component {

    constructor(props) {
        super(props);
        this.state = {
            accounts: [],
            userId: null,
            redirect: false
        };
    }

    async componentDidMount() {
        const userId = this.props.match.params.userId;
        const encrypt = this.props.match.params.encrypt;        
        const url = `${PROXY_CORS}${config.login.URL_DECRYPTED_USER}${userId}`;
        // console.log(`userId -> ${userId}, encrypt -> ${encrypt}, url -> ${url}`);

        if (encrypt === '1') {        
            let base64 = require('base-64');
            let headers = new Headers();
            headers.append('Authorization', 'Basic ' + base64.encode(config.login.USER + ":" + config.login.PWD));

            fetch(url, {
                method:'GET',
                headers: headers
            })
            .then(r => r.json())
            .then(user => {
                user.ID_ENTRADA != null ?
                    console.log(`User [${userId}] exists: ${user.ID_ENTRADA}`):
                    console.log(`User [${userId}] NOT exists`);
                
                this.setState({ userId: user.ID_ENTRADA });

                if (this.state.userId != null) {
                    this.setState({ redirect: false });
                    fetch(`${config.url.API_ACCOUNTS}/user/${this.state.userId}`)
                    .then(r => r.json())
                    .then(data => {
                        this.setState({ data: data });
                        if (data.length === 0){
                            console.log("data -> EMPTY");
                        } else {
                            const account = data[0];
                            if (account.defaultAccount)  {
                                switch (account.provider) {
                                    case INBOX_GOOGLE:                                    
                                            window.open(`${config.url.URL_INBOX_GOOGLE}/${this.state.userId}`, '_self');
                                            break;
                                        case INBOX_OUTLOOK:                                    
                                            window.open(`${config.url.URL_INBOX_OUTLOOK}/${this.state.userId}`, '_self');
                                            break;
                                        case INBOX_IMAP:
                                            window.open(`${config.url.URL_INBOX_IMAP}/${this.state.userId}`, '_self');
                                            break;
                                                
                                        default:
                                            console.log("Valor no válido");
                                            break;                
                                }
                            } 
                        }
                    }); 
                } else {
                    this.setState({ redirect: true });
                }    
            })
            .catch(err => {
                console.log(err);
            });
        } else {
            if (userId != null) {        
                this.setState({ userId: userId });
                this.setState({ redirect: false });

                fetch(`${config.url.API_ACCOUNTS}/user/${this.state.userId}`)
                .then(r => r.json())
                .then(data => {
                    this.setState({ data: data });
                    if (data.length === 0){
                        console.log("data -> EMPTY");
                    } else {
                        const account = data[0];
                        if (account.defaultAccount)  {
                            switch (account.provider) {
                                case INBOX_GOOGLE:                                    
                                        window.open(`${config.url.URL_INBOX_GOOGLE}/${this.state.userId}`, '_self');
                                        break;
                                    case INBOX_OUTLOOK:                                    
                                        window.open(`${config.url.URL_INBOX_OUTLOOK}/${this.state.userId}`, '_self');
                                        break;
                                    case INBOX_IMAP:
                                        window.open(`${config.url.URL_INBOX_IMAP}/${this.state.userId}`, '_self');
                                        break;
                                            
                                    default:
                                        console.log("Valor no válido");
                                        break;                
                            }
                        } 
                    }
                });
            } 
        }
    }

    render() {      
        const { redirect, userId } = this.state;

        if (redirect) {            
            return <Redirect to='/usernotfound'/>;        
        }
          
        return (
            <div className="container-fluid d-flex h-100 flex-column" id="borrar">                
                {/* <Header /> */}
                <GoTo userId={userId} />
                <Footer />
            </div>
        )
    }
}

export default PageGoTo;